
<template>
    <div class="card">
        <mu-refresh-control :refreshing="refreshing" :trigger="trigger" @refresh="refresh"/>
        <div v-if="order.length>0">
            <mu-card v-for="(obj,index) in order" :key="index">
                    <mu-card-header :title="obj.shop.name" :subTitle="obj.creatTime | formatTime">
                        <mu-avatar :src="obj.shop.image" slot="avatar"/>
                        <span  slot="default" class="orderStatus">{{obj.status | orderStatus}}</span>                     
                    </mu-card-header>                   
                    <mu-content-block class="foods">
                      <mu-divider />
                      <mu-content-block class="food" v-for="(food,index) in obj.foods" :key="index" v-show="index<3">
                        <span>{{food.name}}</span>
                        <span>x{{food.count}}</span>                       
                      </mu-content-block>
                      <mu-content-block class="food">
                        <span ><span v-show="obj.foods.length>3">...</span></span>
                        <span >共{{obj.foods.length}}件商品,实付<span class="money">￥{{obj.money}}</span></span>                        
                      </mu-content-block>
                    </mu-content-block>
                    <mu-card-actions v-show="obj.eval===0">
                        <mu-raised-button  label="评价" backgroundColor="#ff9900" @click="evalShow(index)"/>
                    </mu-card-actions>
            </mu-card>
            <mu-infinite-scroll :scroller="scroller" :loading="loading" @load="loadMore" loadingText="正在加载..."/>
        </div>
        <mu-content-block class="empty" v-else>暂无数据</mu-content-block>             
    </div>
</template>
<script>
//vue.esm.js?efeb:591 [Vue warn]: Do not use built-in or reserved HTML elements as component id: circle
import { formatDate } from "@/common/js/date";
export default {
  name: "ordercard",
  props: {
    order: {
      type: Array,
      default() {
        return [];
      }
    }
  },
  filters: {
    formatTime(time) {
      let date = new Date(time);
      return formatDate(date, "yyyy-MM-dd hh:mm");
    },
    orderStatus(status) {
      let orderStatus = null;
      switch (status) {
        case 0:
          orderStatus = "订单已失效";
          break;
        case 1:
          orderStatus = "订单未付款";
          break;
        case 3:
          orderStatus = "订单已完成";
          break;
        default:
          orderStatus = "订单异常";
          break;
      }
      return orderStatus;
    }
  },
  data() {
    const list = [];
    for (let i = 0; i < 10; i++) {
      list.push("item" + (i + 1));
    }
    return {
      list,
      num: 10,
      refreshing: false,
      trigger: null,
      loading: false,
      scroller: null
    };
  },
  mounted() {
    this.trigger = this.$el;
    this.scroller = this.$el;
  },
  methods: {
    evalShow(val){
      this.$emit("click",val);
    },
    refresh() {
      this.refreshing = true;
      setTimeout(() => {
        const list = [];
        for (let i = this.num; i < this.num + 10; i++) {
          list.push("item" + (i + 1));
        }
        this.list = list;
        this.num += 10;
        this.refreshing = false;
      }, 2000);
    },
    loadMore() {
      this.loading = true;
      setTimeout(() => {
        for (let i = this.num; i < this.num + 10; i++) {
          this.list.push("item" + (i + 1));
        }
        this.num += 10;
        this.loading = false;
      }, 2000);
    }
  }
};
</script>

<style lang="scss" scoped>
.mu-card-actions {
  display: flex;
  flex-direction: row-reverse;
}
.mu-card-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  .mu-avatar {
    margin-right: 6px;
  }
}
.card {
  padding-bottom: 100px;
}
.mu-card {
  margin-bottom: 10px;
  &:first-child {
    margin-top: 48px;
  }
}
.mu-card:last-child {
  margin-bottom: 50px;
}
.empty {
  display: flex;
  justify-content: center;
  align-items: center;
  padding-top: 120px;
}
// .mu-content-block {
//     padding: 8px 32px;
//     width: 100%;
// }
.foods {
  padding: 8px 15px 8px 50px;
  .food {
    display: flex;
    justify-content: space-between;
    align-items: center;
    color: rgba(71, 74, 79, 0.54);
    font-size: 14px;
  }
}
.mu-card-header {
  padding: 16px 16px 0;
}
.money {
  font-size: 18px;
  font-family: fantasy;
  padding-left: 10px;
  color: #1c2438;
}
</style>



