<template>
  <transition name="fade">
    <div class="evaluate" v-if="evalShow">
      <mu-card >
        <mu-card-header  :title="evalObj.shop.name" :subTitle="evalObj.shop.creatTime | formatTime" >
          <mu-avatar  :src="evalObj.shop.image" slot="avatar"/>
        </mu-card-header>
        
        <mu-content-block>"{{starEval}}"</mu-content-block>
        
        <mu-content-block>
          <star :size="48" :score="starNum" @click="tapStar"/>
        </mu-content-block>
        <mu-sub-header>评论</mu-sub-header>
          <mu-text-field v-model="evalText" class="evalInput" hintText="不允许超过50个字符" :errorText="multiLineInputErrorText" @textOverflow="handleMultiLineOverflow" multiLine :rows="3" :rowsMax="6" :maxLength="50"/>
        <mu-list >
          <mu-sub-header>喜爱</mu-sub-header>
          <mu-list-item disableRipple  :title="food.name" v-for="(food,index) in evalObj.foods" :key="index">
            <mu-checkbox v-model="food.like" uncheckIcon="favorite_border" checkedIcon="favorite" slot="left"/>
          </mu-list-item>
        </mu-list>
        <mu-card-actions>
          
          <mu-raised-button label="提交" backgroundColor="#ff9900" @click="submit"/>
          <mu-flat-button label="返回" @click="evalBack"/>
        </mu-card-actions>
      </mu-card>
    </div>
  </transition>
</template>
<script>
/**
 * @description 后期增加分页
 */
import star from "@/components/star";
import { formatDate } from "@/common/js/date";
export default {
  name: "evaluate",
  data() {
    return {
      starNum: 3,
      starEval: "满意",
      multiLineInput: "",
      multiLineInputErrorText: "",
      evalText:null
    };
  },
  filters: {
    formatTime(time) {
      let date = new Date(time);
      return formatDate(date, "yyyy-MM-dd hh:mm");
    }
  },
  props: {
    evalShow: {
      type: Boolean,
      default: false
    },
    evalObj: {
      type: Object
    }
  },
  methods: {
    submit(){
      this.$emit("submit",{
        text:this.evalText,
        score:this.starNum,
        evalStatus:1,
      });
      
    },
    handleMultiLineOverflow(isOverflow) {
      this.multiLineInputErrorText = isOverflow ? "超过啦！！！！" : "";
    },
    tapStar(index) {
      let starText = ["很差", "一般", "满意", "非常满意", "无可挑剔"];
      this.starEval = starText[index];
      this.starNum = index + 1;
    },
    evalBack() {
      this.$emit("evalBack");
    }
  },
  components: {
    star
  }
};
</script>
<style lang="scss" scoped>
.evaluate {
  position: fixed;
  left: 0;
  top: 0;
  bottom: 0;
  z-index: 10000;
  width: 100%;
  background: #fff;
  transform: translate3d(0, 0, 0);

  &.fade-enter-active,
  &.fade-leave-active {
    transition: all 0.2s linear;
  }

  &.fade-enter,
  &.fade-leave-active {
    transform: translate3d(100%, 0, 0);
  }
  .mu-card {
    // width: 90%;
    // margin: 10px auto;
    height: 100%;
    .mu-content-block {
      text-align: center;
    }
    .evalInput {
      display: flex;
      /* justify-content: space-around; */
      width: 90%;
      margin: 0px auto;
    }
    .mu-card-actions {
      display: flex;
      flex-direction: row-reverse;
    }
  }
}
</style>



